@import url(https://fonts.googleapis.com/css?family=Lato&display=swap);
@import url(https://fonts.googleapis.com/css?family=Pacifico&display=swap);
.navbar{box-shadow:0px 0px 6px 0px rgba(0,0,0,0.75);background-color:#f9f9f9 !important;padding:1em}.navbar .navbar-brand{font-family:"Pacifico", sans-serif;color:#333}.nav{margin-bottom:10px}.nav .nav-link{color:#333;margin:0px 5px;-webkit-transition:background-color 0.3s;transition:background-color 0.3s;border-radius:0.5rem}.nav .nav-link:hover{background-color:#e3e3e3;color:#333}body{margin:0;font-family:"Lato", "Helvetica", Helvetica, sans-serif}body .main-container{margin-top:120px}body .search-input{font-weight:bold;width:100%;height:40px;padding:6px 15px;border-radius:5px;outline:none;border:none;margin-bottom:1em;background:#f6f7f9;color:#748194;font-size:14px}.close{width:32px;height:32px;opacity:0.8}.close:hover{opacity:1;cursor:pointer}.close:before,.close:after{position:absolute;left:15px;content:" ";height:26px;width:2px;background-color:#333}.close:before{-webkit-transform:rotate(45deg);transform:rotate(45deg)}.close:after{-webkit-transform:rotate(-45deg);transform:rotate(-45deg)}.card{box-shadow:0px 0px 3px 0px #aaa;border-radius:0.5em;margin:7px 0px;border:none}.card:hover{box-shadow:0px 0px 5px 0px #aaa;cursor:pointer}.card img{max-height:150px;margin:2px;background-size:cover}.card a{color:#333333;margin:0px 5px;-webkit-transition:background-color 0.3s;transition:background-color 0.3s;border-radius:0.5rem;margin-bottom:1em}.modal-dialog{max-width:700px}.modal-dialog .modal-content{border:0px 0px 6px 0px rgba(0,0,0,0.75);border-radius:0 !important}.modal-dialog .modal-content span{position:absolute;top:10px;right:23px;z-index:2}.modal-dialog .modal-content spanhover{cursor:pointer}.modal-dialog .modal-content .modal-body{padding:0}.modal-dialog .modal-content .modal-body .modal-image{background-size:cover;margin:0;width:100%}.fa-times,.fa-chevron-left,.fa-chevron-right{text-shadow:0px 1px 3px #000;color:#f9f9f9}.modal-footer-info{padding:0.75rem;border-top:1px solid #f9f9f9;border-bottom-right-radius:calc(0.3rem - 1px);border-bottom-left-radius:calc(0.3rem - 1px)}.modal-footer-info .modal-info{display:flex;flex-direction:row;justify-content:space-between}.modal-footer-info .modal-footer-container{display:flex;flex-direction:row;padding-top:10px;justify-content:space-between}.modal-footer-info .modal-footer-container p{margin:0px 5px}.modal-footer-info .modal-footer-container img{width:23px;margin-left:5px}.btn-link{color:#333 !important}.btn-link .dropdown-menu{box-shadow:0px 0px 6px 0px rgba(0,0,0,0.75);border:none}hr{margin:0 !important}.tag{margin:0px 5px}.carousel img{max-height:150px}.carousel #chevron-arrow-left{display:inline-block;border-right:4px solid #333;border-bottom:4px solid #333;width:10px;height:10px;-webkit-transform:rotate(-225deg);transform:rotate(-225deg)}.carousel #chevron-arrow-right{display:inline-block;border-right:4px solid #333;border-bottom:4px solid #333;width:10px;height:10px;-webkit-transform:rotate(-45deg);transform:rotate(-45deg)}@media (max-width: 600px){.modal-image{background-size:cover;margin:0;width:auto;max-height:300px}.carousel-inner{margin-top:20px}}

